import React from "react";
import Background from "components/Background";
import NavBar from "components/NavBar";
import "../../css/CryLog.css";

export default class CryLog extends React.Component {
    async parseData() {
        let sheetsMacro =
            "https://script.google.com/macros/s/AKfycbyiYX1QwdSSArOY8umH-xAn1JzNuXis-D97rNi_qEyWdjCfEkrflSmLZdMcomh7lPb1/exec";
        const param = {
            spreadsheetId: "1uKsKzbuesHZ-vdmJntESZD4tjTU_iyh6flAnZ7tRqkE",
            sheetName: "Cry Log",
        };

        const q = new URLSearchParams(param);
        const url = sheetsMacro + "?" + q.toString();
        const response = await fetch(url);
        let data = await response.json();

        data = data.values;
        let logs = [];
        for (let i = 1; i < data.length; i++) {
            logs.push({
                name:
                    data[i][0]
                        .split(" ")[0]
                        .toLowerCase()
                        .charAt(0)
                        .toUpperCase() +
                    data[i][0].split(" ")[0].toLowerCase().substring(1),
                date: data[i][1],
                time: data[i][2],
                length: data[i][3],
                reason: data[i][4],
                notes: data[i][5],
            });
        }

        // get rid of logs were name, date, time, or length are undefined
        logs = logs.filter((log) => {
            return (
                log.name !== "" &&
                log.date !== "" &&
                log.time !== "" &&
                log.length !== ""
            );
        });

        // sort logs by date
        logs.sort((a, b) => {
            return new Date(a.date) - new Date(b.date);
        });

        // go through and convert date to a string, length to a number of minutes, and time to a string
        for (let i = 0; i < logs.length; i++) {
            logs[i].date = new Date(logs[i].date).toLocaleDateString();
            logs[i].unixDate = new Date(logs[i].date).getTime();
            let lengthStr = logs[i].length;

            if (lengthStr.includes("h")) {
                let hours = parseFloat(lengthStr.split("h")[0]);
                logs[i].length = hours * 60;
            } else {
                logs[i].length = parseFloat(lengthStr.split("m")[0]);
            }

            logs[i].time = new Date(logs[i].time).toLocaleTimeString();
        }

        let lengthsPerDay = {};

        for (let log of logs) {
            if (lengthsPerDay[log.unixDate] === undefined) {
                lengthsPerDay[log.unixDate] = log.length;
            } else {
                lengthsPerDay[log.unixDate] += log.length;
            }
        }

        // split this up into weeks
        let weeks = {};
        let currentWeek = [];
        let currentWeekStart = logs[0].unixDate;
        let currentWeekEnd = currentWeekStart + 6 * 24 * 60 * 60 * 1000;

        for (let log of logs) {
            if (log.unixDate < currentWeekEnd) {
                currentWeek.push(log);
            } else {
                weeks[currentWeekStart] = currentWeek;
                currentWeek = [log];
                currentWeekStart = log.unixDate;
                currentWeekEnd = currentWeekStart + 6 * 24 * 60 * 60 * 1000;
            }
        }

        weeks[currentWeekStart] = currentWeek;

        let weekLengths = {};
        for (let week in weeks) {
            let totalLength = 0;
            for (let log of weeks[week]) {
                if (isNaN(log.length)) continue;
                totalLength += log.length;
            }

            weekLengths[week] = totalLength;
        }

        // get cry total time per person
        let cryTotal = {};
        for (let log of logs) {
            if (isNaN(log.length)) continue;
            if (cryTotal[log.name] === undefined) {
                cryTotal[log.name] = log.length;
            } else {
                cryTotal[log.name] += log.length;
            }
        }

        // get total times cried per person
        let cryCount = {};
        for (let log of logs) {
            if (cryCount[log.name] === undefined) {
                cryCount[log.name] = 1;
            } else {
                cryCount[log.name] += 1;
            }
        }

        this.logs = logs;
        this.lengthsPerDay = lengthsPerDay;
        this.weekLengths = weekLengths;
        this.cryTotal = cryTotal;
        this.cryCount = cryCount;

        this.colors = [
            "#e803fc",
            "#f259ff",
            "#ee9af5",
            "#92709c",
            "#f2c1ff",
            "#5e336b",
            "#9516a1",
        ];
    }

    async componentDidMount() {
        await this.parseData();
        // shades of purple in rgb():
        this.colors = [
            "rgb(232, 3, 252)",
            "rgb(242, 89, 255)",
            "rgb(238, 154, 245)",
            "rgb(146, 112, 156)",
            "rgb(242, 193, 255)",
            "rgb(94, 51, 107)",
            "rgb(149, 22, 161)",
            "rgb(255, 0, 255)",
            "rgb(255, 128, 255)",
            "rgb(230, 0, 255)",
        ];

        this.updateStats();
        this.drawPieCharts();
        this.addDropdownOptions();
        this.onSelectNewPerson();
        this.drawTimeCryGraph();
    }

    updateStats() {
        let timeCried = 0;
        let numCries = 0;
        let longestCry = 0;
        let averageLength = 0;

        let byName = {};

        for (let log of this.logs) {
            numCries += 1;
            if (isNaN(log.length)) continue;
            timeCried += log.length;
            if (log.length > longestCry) {
                longestCry = log.length;
            }

            if (byName[log.name] === undefined) {
                byName[log.name] = {
                    time: log.length,
                    count: 1,
                };
            } else {
                byName[log.name].time += log.length;
                byName[log.name].count += 1;
            }
        }

        averageLength = timeCried / numCries;
        let topCrier = "";
        let topCrierTime = 0;
        // let topCrierCount = 0;

        for (let name in byName) {
            if (byName[name].time > topCrierTime) {
                topCrier = name;
                topCrierTime = byName[name].time;
                // topCrierCount = byName[name].count;
            }
        }

        document.getElementById("time-cried-p").innerHTML = `${
            Math.round((timeCried / 60) * 100) / 100
        } hours`;
        document.getElementById("num-cries-p").innerHTML = `${numCries}`;
        document.getElementById(
            "avg-cry-p"
        ).innerHTML = `${averageLength.toFixed(2)} minutes`;
        document.getElementById("longest-cry-p").innerHTML = `${
            Math.round((longestCry / 60) * 100) / 100
        } hours`;
        document.getElementById("top-crier-p").innerHTML = `${topCrier} - ${
            Math.round((topCrierTime / 60) * 100) / 100
        } hours`;
    }

    drawPieCharts() {
        let total = 0;
        for (let name in this.cryTotal) {
            total += this.cryTotal[name];
        }
        // this is is for the total time cried
        let pieData = [];
        for (let name in this.cryTotal) {
            pieData.push({
                name: name.split(" ")[0],
                value: this.cryTotal[name],
            });
        }

        let color_names = {};

        pieData.sort((a, b) => {
            return b.value - a.value;
        });

        this.pieChartHover("percent-of-time-chart", pieData);

        let curPercent = 0;
        let css = "conic-gradient( from 0deg, ";
        for (let i = 0; i < pieData.length; i++) {
            let color = this.colors[i];
            color_names[pieData[i].name] = color;
            let percent = pieData[i].value / total + curPercent;
            curPercent = percent;
            css += `${color} 0 ${percent * 100}%, `;
        }
        css = css.slice(0, -2);
        css += ")";

        let elm = document.getElementById("percent-of-time-chart");
        elm.style.backgroundImage = css;

        // number of times cried (keeps same colors)

        total = 0;

        pieData = [];
        for (let name in this.cryCount) {
            total += this.cryCount[name];
            pieData.push({
                name: name.split(" ")[0],
                value: this.cryCount[name],
            });
        }

        pieData.sort((a, b) => {
            return b.value - a.value;
        });

        this.pieChartHover("num-of-cries-chart", pieData);

        curPercent = 0;
        css = "conic-gradient( from 0deg, ";
        for (let i = 0; i < pieData.length; i++) {
            let color = color_names[pieData[i].name];
            let percent = pieData[i].value / total + curPercent;
            curPercent = percent;
            css += `${color} 0 ${percent * 100}%, `;
        }
        css = css.slice(0, -2);
        css += ")";
        elm = document.getElementById("num-of-cries-chart");
        elm.style.backgroundImage = css;

        // make the key
        let key = document.getElementById("pie-chart-key");
        for (let name in color_names) {
            let div = document.createElement("div");
            div.className = "key-item";
            div.style.backgroundColor = color_names[name];
            let p = document.createElement("p");
            p.innerHTML = name.split(" ")[0];
            div.appendChild(p);
            key.appendChild(div);
        }
    }

    pieChartHover(piechartid, data) {
        let element = document.getElementById(piechartid);
        document.addEventListener("mousemove", (e) => {
            let x = e.pageX - element.offsetLeft;
            let y = e.pageY - element.offsetTop;
            let width = element.offsetWidth;
            let height = element.offsetHeight;
            let xPercent = x / width;
            let yPercent = y / height;
            let angle = Math.atan2(yPercent - 0.5, xPercent - 0.5);
            angle = angle * (180 / Math.PI);
            angle = angle + 90;
            if (angle < 0) {
                angle += 360;
            }
            let total = 0;
            for (let i = 0; i < data.length; i++) {
                total += data[i].value;
            }

            // if it is off the chart, don't show the tooltip
            if (xPercent < 0 || xPercent > 1 || yPercent < 0 || yPercent > 1) {
                document.getElementById(`${piechartid}-tooltip`).style.display =
                    "none";

                // hide the lable
                element.getElementsByClassName("pie-lable")[0].style.display =
                    "block";

                return;
            } else {
                element.getElementsByClassName("pie-lable")[0].style.display =
                    "none";
            }

            let curPercent = 0;
            for (let i = 0; i < data.length; i++) {
                let percent = data[i].value / total + curPercent;
                if (angle < percent * 360) {
                    let name = data[i].name;
                    let value = data[i].value;
                    let percent = Math.round((value / total) * 10000) / 100;
                    let tooltip = document.getElementById(
                        `${piechartid}-tooltip`
                    );
                    if (piechartid === "percent-of-time-chart") {
                        tooltip.innerText = `${name} - ${percent}% - ${
                            Math.round((value * 100) / 60) / 100
                        } hours`;
                    }
                    if (piechartid === "num-of-cries-chart") {
                        tooltip.innerText = `${name} - ${percent}% - ${value} times`;
                    }

                    let elmx, elmy;
                    if (piechartid === "percent-of-time-chart") {
                        elmx = document.getElementById(
                            "percent-of-time-chart"
                        ).offsetLeft;
                        elmy = document.getElementById(
                            "percent-of-time-chart"
                        ).offsetTop;
                    }
                    if (piechartid === "num-of-cries-chart") {
                        elmx =
                            document.getElementById(
                                "num-of-cries-chart"
                            ).offsetLeft;
                        elmy =
                            document.getElementById(
                                "num-of-cries-chart"
                            ).offsetTop;
                    }

                    tooltip.style.left = `${x + elmx}px`;
                    tooltip.style.top = `${y + elmy - 30}px`;
                    tooltip.style.display = "block";
                    break;
                }
                curPercent = percent;
            }
        });
    }

    addDropdownOptions() {
        let dropdown = document.getElementById("person-dropdown");
        let names = [];
        for (let name in this.cryTotal) {
            names.push(name);
        }

        names.sort();

        for (let name of names) {
            let option = document.createElement("option");
            option.value = name;
            option.innerHTML = name;
            dropdown.appendChild(option);
        }

        dropdown.addEventListener("change", () => {
            this.onSelectNewPerson();
        });
    }

    onSelectNewPerson() {
        let person = document.getElementById("person-dropdown").value;
        document.getElementsByClassName("bar-title")[0].innerHTML = person;

        let timeCried = 0;
        let numCries = 0;
        let longestCry = 0;
        let averageLength = 0;

        let logs = this.logs.filter((log) => {
            return log.name === person;
        });

        for (let log of logs) {
            numCries += 1;
            if (isNaN(log.length)) continue;
            timeCried += log.length;
            if (log.length > longestCry) {
                longestCry = log.length;
            }
        }

        averageLength = timeCried / numCries;

        document.getElementById("person-time-cried-p").innerHTML = `${
            Math.round((timeCried / 60) * 100) / 100
        } hours`;
        document.getElementById("person-num-cries-p").innerHTML = `${numCries}`;
        document.getElementById(
            "person-avg-cry-p"
        ).innerHTML = `${averageLength.toFixed(2)} minutes`;
        document.getElementById("person-longest-cry-p").innerHTML = `${
            Math.round((longestCry / 60) * 100) / 100
        } hours`;
    }

    cubicSplineInterpolation(points) {
        const n = points.length;
        const h = [];
        const alpha = [];
        const l = [];
        const mu = [];
        const z = [];
        const c = [];
        const b = [];
        const d = [];

        // Step 1: Compute the differences in x values and y values
        for (let i = 0; i < n - 1; i++) {
            h[i] = points[i + 1][0] - points[i][0];
            alpha[i] =
                (3 / h[i]) * (points[i + 1][1] - points[i][1]) -
                (3 / h[Math.max(0, i - 1)]) *
                    (points[i][1] - points[Math.max(0, i - 1)][1]);
        }

        // Step 2: Solve the tridiagonal linear system for z values
        l[0] = 1;
        mu[0] = 0;
        z[0] = 0;

        for (let i = 1; i < n - 1; i++) {
            l[i] =
                2 * (points[i + 1][0] - points[i - 1][0]) -
                h[i - 1] * mu[i - 1];
            mu[i] = h[i] / l[i];
            z[i] = (alpha[i] - h[i - 1] * z[i - 1]) / l[i];
        }

        l[n - 1] = 1;
        z[n - 1] = 0;
        c[n - 1] = 0;

        // Step 3: Back-substitute to find the coefficients c, b, and d
        for (let j = n - 2; j >= 0; j--) {
            c[j] = z[j] - mu[j] * c[j + 1];
            b[j] =
                (points[j + 1][1] - points[j][1]) / h[j] -
                (h[j] * (c[j + 1] + 2 * c[j])) / 3;
            d[j] = (c[j + 1] - c[j]) / (3 * h[j]);
        }

        // Generate the piecewise polynomial function
        function splineFunction(x) {
            function findInterval(x, points) {
                for (let i = 0; i < points.length - 1; i++) {
                    if (x >= points[i][0] && x <= points[i + 1][0]) {
                        return i;
                    }
                }
                return points.length - 2;
            }

            const i = findInterval(x, points);
            const dx = x - points[i][0];
            const value =
                points[i][1] + b[i] * dx + c[i] * dx ** 2 + d[i] * dx ** 3;
            return value;
        }

        return splineFunction;
    }

    // Helper function to find the interval of x

    drawTimeCryGraph() {
        let weeksLengths = this.weekLengths;
        // let origin = weeksLengths[0];
        let weeks = [];
        let weekStrings = [];
        let lengths = [];
        for (let week in weeksLengths) {
            weeks.push(week);
            lengths.push(weeksLengths[week]);
            weekStrings.push(new Date(parseInt(week)).toLocaleDateString());
        }

        let NRWeeks = weeks.length;

        let points = [];
        for (let i = 0; i < NRWeeks; i++) {
            points.push([i, weeksLengths[weeks[i]]]);
        }

        let spline = this.cubicSplineInterpolation(points);

        let canvas = document.getElementById("time-cry-graph");
        let ctx = canvas.getContext("2d");

        // set canvas width to #graph width and height
        let graph = document.getElementById("graph");
        canvas.width = graph.offsetWidth;
        canvas.height = graph.offsetHeight;

        let width = canvas.width;
        let height = canvas.height;

        let xScale = width / NRWeeks / 1.05;
        let yScale = height / Math.max(...lengths) / 1.1;

        // draw the path

        // style
        ctx.strokeStyle = "#ffffff";
        ctx.lineWidth = 5;
        for (let i = 0; i < NRWeeks; i += 0.01) {
            let x = Math.round(i * xScale);
            let y = height - Math.round(spline(i) * yScale) - height * 0.05;
            ctx.lineTo(x, y);
        }

        ctx.stroke();

        let gradA = "#dc82f8";
        let gradB = "rgba(220, 130, 248, .2)";

        // draw a gradient below the graph
        let gradient = ctx.createLinearGradient(0, 0, 0, height);
        gradient.addColorStop(0, gradA);
        gradient.addColorStop(1, gradB);
        ctx.fillStyle = gradient;
        ctx.lineTo(width * 0.95, height);
        ctx.lineTo(0, height);
        ctx.fill();

        // draw x axis
        ctx.beginPath();
        ctx.strokeStyle = "#ffffff";
        ctx.lineWidth = 5;
        ctx.moveTo(0, height);
        ctx.lineTo(width, height);
        ctx.stroke();

        // draw y axis
        ctx.beginPath();
        ctx.strokeStyle = "#ffffff";
        ctx.lineWidth = 5;
        ctx.moveTo(width * 0.95, 0);
        ctx.lineTo(width * 0.95, height);
        ctx.stroke();

        // draw the y axis labels
        ctx.font = "20px Arial";
        ctx.fillStyle = "#ffffff";
        ctx.textAlign = "right";
        ctx.textBaseline = "middle";

        let max = Math.max(...lengths);
        let min = Math.min(...lengths);

        for (let i = 0; i < 6; i++) {
            let num = Math.round(
                (Math.round(((max - min) / 6) * 100) / 100) * i
            );
            let y = height - Math.round((num / max) * height) - height * 0.05;
            ctx.fillText(num, width * 0.95 - 10, y);
        }

        // draw the y axis title rotated 90 degrees
        ctx.save();
        ctx.translate(width * 0.95 + 25, height / 2);
        ctx.rotate(Math.PI / 2);
        ctx.font = "40px Arial";
        ctx.textAlign = "center";
        ctx.fillText("Minutes", 0, 0);
        ctx.restore();

        // load the x axis canvas
        let xaxis = document.getElementById("x-axis-labels");
        let xctx = xaxis.getContext("2d");

        // set canvas width to #graph width and height
        xaxis.width = graph.offsetWidth;
        xaxis.height = graph.offsetHeight / 5;

        // draw the x axis labels
        xctx.font = "20px Arial";
        xctx.fillStyle = "#ffffff";
        xctx.textAlign = "center";
        xctx.textBaseline = "middle";

        // write the labels rotated 90 degrees
        xctx.save();
        xctx.translate(xaxis.width + 10, xaxis.height / 2);
        xctx.rotate(Math.PI / 2);
        for (let i = 0; i < NRWeeks; i++) {
            let x = Math.round(i * xScale);
            xctx.fillText(weekStrings[i], 0, width - x);
        }
        xctx.restore();
    }

    render() {
        return (
            <div className="about">
                <Background />
                <NavBar />
                <h1>Cry Log Analysis</h1>
                <div id="container">
                    <div id="statistics">
                        <h2>Statistics</h2>
                        <div id="stats">
                            <div className="stat">
                                <h3>Time Cried</h3>
                                <div className="line"></div>
                                <p id="time-cried-p"></p>
                            </div>
                            <div className="stat">
                                <h3>Number of Cries</h3>
                                <div className="line"></div>
                                <p id="num-cries-p"></p>
                            </div>
                            <div className="stat">
                                <h3>Average Cry Length</h3>
                                <div className="line"></div>
                                <p id="avg-cry-p"></p>
                            </div>
                            <div className="stat">
                                <h3>Longest Cry</h3>
                                <div className="line"></div>
                                <p id="longest-cry-p"></p>
                            </div>
                            <div className="stat">
                                <h3>Top Crier</h3>
                                <div className="line"></div>
                                <p id="top-crier-p"></p>
                            </div>
                        </div>
                    </div>
                    <div id="pie-and-stats">
                        <div id="per-person-stats">
                            <div id="per-person-stats-container">
                                <div id="top-bar">
                                    <div className="bar-title">Name</div>
                                    <select id="person-dropdown"></select>
                                </div>
                                <div id="person-statistics">
                                    <div className="person-stat">
                                        <h3>Time Cried</h3>
                                        <div className="line"></div>
                                        <p id="person-time-cried-p"></p>
                                    </div>
                                    <div className="person-stat">
                                        <h3>Number of Cries</h3>
                                        <div className="line"></div>
                                        <p id="person-num-cries-p"></p>
                                    </div>
                                    <div className="person-stat">
                                        <h3>Average Cry Length</h3>
                                        <div className="line"></div>
                                        <p id="person-avg-cry-p"></p>
                                    </div>
                                    <div className="person-stat">
                                        <h3>Longest Cry</h3>
                                        <div className="line"></div>
                                        <p id="person-longest-cry-p"></p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div id="pie-charts">
                            <div id="percent-of-time-chart">
                                <h3 className="pie-lable">
                                    Percent of Time Cried
                                </h3>
                                <p
                                    id="percent-of-time-chart-tooltip"
                                    className="pie-tooltip"></p>
                            </div>
                            <div id="num-of-cries-chart">
                                <h3 className="pie-lable">Number of Cries</h3>
                                <p
                                    id="num-of-cries-chart-tooltip"
                                    className="pie-tooltip"></p>
                            </div>
                            <div id="pie-chart-key"></div>
                        </div>
                    </div>
                    <div id="graphs">
                        <div id="graph-container">
                            <div id="graph-title">
                                <h2>Minutes Per Week</h2>
                            </div>
                            <div id="graph">
                                <canvas id="time-cry-graph"></canvas>
                                <canvas id="x-axis-labels"></canvas>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
